@import "./card.css";
@import "./stars.css";

body,
p {
  margin: 0;
  font-family: "Roboto", Arial, sans-serif;
}

.body__card {
  display: flex;
  box-sizing: border-box;
  width: 200px;
  flex-direction: column;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  padding-left: 16px;
  padding-right: 16px;
}
